import { Component, Input, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import * as _ from 'lodash';
import { ToastrService } from 'ngx-toastr';
import { CONFIG } from 'src/app/config';
import { MainService } from 'src/app/services/main.service';
import { PaymentsService } from '../payments.service';
enum paymentTypes {
  Personal= 'Personal',
  Business = 'Business'
}

@Component({
  selector: 'ibss-payment-details',
  templateUrl: './payment-details.component.html',
  styleUrls: ['./payment-details.component.scss']
})

export class PaymentDetailsComponent implements OnInit {
  
  @Input('isMainSite') isMainSite:any = false;

  config: typeof CONFIG = CONFIG;
  allPlans =[];
  plansDetail: any = [];
  paymentStep = 1;
  paymentTypes = paymentTypes;
  paymentType = this.paymentTypes.Personal;
  showPlans = false;
  subscriptionDetails: any = null;
  generalPlans:any = [];
  planIcons = {
    "Free":"../../../assets/Payment-Page/free.png",
    "Basic":"../../../assets/Payment-Page/basic.png",
    "Advanced":"../../../assets/Payment-Page/advanced.png",
    "Premium":"../../../assets/Payment-Page/premium.png"
  }

  constructor(
    private payServices: PaymentsService,
    private toastr: ToastrService,
    private router: Router,
    private ms: MainService
  ) { }

  ngOnInit(): void {
    if (this.isMainSite) {
      this.showPlans = true;
    } else {
      this.getSubscriptionDetails();
    }
    this.payServices.getSubscriptionPlans()
    .subscribe((res: any)=> {
      console.log("getDashboardData", res);
      console.log("verfiyOTP", res);
      if (res.error && res.error.responseCode === 0) {
        let tempRes = JSON.parse(JSON.stringify(res))
        if (tempRes.serviceData && tempRes.serviceData.length > 0) {
              this.allPlans = tempRes.serviceData;

              let paymentTypes = this.paymentTypes.Personal;

              if (this.config.isLoggedin) {
                paymentTypes = this.config.token.roleType === "PU" ? this.paymentTypes.Personal : this.paymentTypes.Business;
              }

              this.toggleType(paymentTypes);
            }
          } else {
            if (res.error) {
              if (res.error.businessValidation.length > 0) {
                for (let i = 0; i < res.error.businessValidation.length; i++) {
                  this.toastr.error(res.error.businessValidation[i].errorDesc);
                }
              }
              if (res.error.dataValidation.length > 0) {
                for (let i = 0; i < res.error.dataValidation.length; i++) {
                  this.toastr.error(res.error.dataValidation[i].errorDesc);
                }
              }
            } else if (res.responseCode == -1) {
              this.toastr.error("Please try again after sometimes");
            }
          }
          this.config.toggleLoader(false);
    }, (err)=> {
      this.config.toggleLoader(false);
      this.toastr.error("Please try again after sometimes");
    });
  }

  toggleType(type: any) {
    this.paymentType = type;
    this.plansDetail = this.allPlans.filter((plan:any)=>{ 
      if (plan.planType == type) {
        if (this.config.isLoggedin) {
          if (plan.planName == "Free") {
            return false;
          } else {
            return this.config.token.roleType == plan.userRoleType;
          }
            
        }
        return true;
      }

      return false;
    });
    
    this.plansDetail.sort((a:any, b:any)=> a.pricingMaster[0].price - b.pricingMaster[0].price)
  }

  onChoosePlan(plan: any) {
    // this.payServices.setSiteStatus(this.isMainSite);


    this.generalPlans = this.allPlans.filter((val:any)=>{ 
      return val.planType == 'General' && val.userRoleType == plan.userRoleType;
    });

    if (plan.pricingMaster[0].price>0) {
      this.config._isMainSite = this.isMainSite;
      this.payServices.setPaymentsDetails(plan, this.generalPlans)
      .subscribe((res) => {
        if (this.config.isLoggedin) {
          this.router.navigate(['/main', 'payments', 'ordersummary']);
        } else {
          this.router.navigate(['ordersummary']);
        }
      });
    } else {
      this.router.navigate(['/get-started']);
    }
  
  }

  getSubscriptionDetails() {
    this.config.toggleLoader(true);
    this.ms.getDashboardData()
      .subscribe((res: any) => {
        console.log("getDashboardData", res);
        console.log("verfiyOTP", res);
        if (res.error && res.error.responseCode === 0) {
          let tempRes = JSON.parse(JSON.stringify(res))
          delete tempRes.serviceData.dashBoardDetails;
          this.subscriptionDetails = {
            availableCredits: _.get(tempRes.serviceData, 'availableCredits', 0),
            availableStorageSpace: _.get(tempRes.serviceData, 'availableStorageSpace', 0),
            planName: _.get(tempRes.serviceData, 'planName', "BASIC"),
            totalNoOfCredits: _.get(tempRes.serviceData, 'totalNoOfCredits', 0)
          }
          this.config.subscriptionDetails = this.subscriptionDetails;
        } else {
          if (res.error) {
            if (res.error.businessValidation.length > 0) {
              for (let i = 0; i < res.error.businessValidation.length; i++) {
                this.toastr.error(res.error.businessValidation[i].errorDesc);
              }
            }
            if (res.error.dataValidation.length > 0) {
              for (let i = 0; i < res.error.dataValidation.length; i++) {
                this.toastr.error(res.error.dataValidation[i].errorDesc);
              }
            }
          } else if (res.responseCode == -1) {
            this.toastr.error("Please try again after sometimes");
          }
        }
        this.config.toggleLoader(false);
      }, (err) => {
        this.config.toggleLoader(false);
        this.toastr.error("Please try again after sometimes");
      });
  }

  onContactUs() {

  }
}
